import React, { useEffect, useState } from "react"
import { useTranslation } from "react-i18next"
import styled from "styled-components"
import { Button, Input, InputGroup, InputGroupText } from "reactstrap"
import MyIcon from "src/core/components/MyIcon/MyIcon"
import { HApi, HAuth, HConstant, HUI, HUtils } from "src/core/helpers"
import Setting2FAHelpers from "../helpers"

const DivTitle = styled.div`
  font-size: 20px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 1rem;
`
const DivDes = styled.div`
  margin-bottom: 1rem;
  color: #6d6d6d;
`

function Generation2FA(props) {
  const { t } = useTranslation()
  const [code, setCode] = useState("")
  const [loadingVerfiy, setLoadingVerfiy] = useState(false)

  const onRequestVerify = () => {
    setLoadingVerfiy(true)
    HApi.apiPost({
      url: "/oauth2/2FAVerify",
      data: {
        UniqueId:
          Setting2FAHelpers.UniqueId || HAuth.getInfo2FA()["user_uniqueid"],
        InputCode: code,
        VerifyToken: HAuth.getInfo2FA()["fa2_token"],
      },
    })
      .then(response => {
        setLoadingVerfiy(false)
        //OK
        HAuth.authLoginWithRes(response)
        HUI.hideInMyModalFromProps(props)
        HUtils.runFuntion(
          HUtils.get(props, "fnList"),
          HConstant.Auth.dispatchLoginSuccess,
          [response]
        )
      })
      .catch(error => {
        setLoadingVerfiy(false)
      })
  }

  useEffect(() => {}, [])

  // console.warn("render verify", props)
  return (
    <div>
      <div className="d-flex flex-column">
        <DivTitle>{t("setting2fa:title.verify")}</DivTitle>
        <DivDes>{t("setting2fa:disable.enter code")}</DivDes>
        <InputGroup style={{ marginBottom: "1rem" }}>
          <InputGroupText>
            <MyIcon data="type:HiOutlineLockClosed" />
          </InputGroupText>
          <Input
            placeholder={t("setting2fa:generation.placeHolderVerify")}
            value={code}
            autoFocus
            onChange={ev => {
              setCode(ev.currentTarget.value)
            }}
            onKeyDown={ev => {
              HUtils.onKeyDownEnter(ev, () => {
                onRequestVerify()
              })
            }}
          />
        </InputGroup>
        <div>
          <Button
            color="primary"
            disabled={loadingVerfiy}
            onClick={() => {
              onRequestVerify()
            }}
          >
            {t("btn.verify")}
          </Button>
        </div>
      </div>
    </div>
  )
}

export default Generation2FA
